* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 10%;
  text-align: center;
}

button {
  padding: 1rem;
  margin: 1rem;
}

.logout-button {
  position: absolute;
  right: 3rem;
  top: 3rem;
}

/* PLANT IMAGES */

.plant-avatar-image-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 10rem;
}

.plant-avatar-image-frame,
.plant-avatar-image-flower,
.plant-avatar-image-background,
.plant-avatar-image-stem,
.plant-avatar-image-addons {
  position: absolute;
  transform: scale(0.5);
}
